<template>
  <div class="App">
    <h1>Seed</h1>
    <input v-model="seedText" />
    <h1>Size</h1>
    <h3>Should be ok with 5-20, sanity check is not done yet.</h3>
    <input v-model="sizeInput" />
    <button
      style="display: block; margin: 20px auto"
      @click="reset"
      type="button"
    >
      reset
    </button>
    <h1>{{ time }}</h1>

    <hex-grid width="1200" height="900" viewBox="-25 -10 100 100">
      <layout
        :size="{ x: 40.0 / size, y: 40.0 / size }"
        :spacing="1.05"
        :origin="{ x: 0, y: 0 }"
      >
        <hexagon
          v-for="(hex, i) in hexagons"
          :key="i"
          :q="hex.q"
          :r="hex.r"
          :s="hex.s"
          :style="{
            fill: hex.color,
            stroke: hex.strokeColor,
            'stroke-width': 3.0 / size,
            'stroke-opacity': hex.stroke ? 0.5 : 0,
          }"
          @click="onClick"
        />
      </layout>
    </hex-grid>
  </div>
  <div
    v-show="selected != -1"
    style="position: absolute; transform: translate(50%, -50%)"
    :style="styles"
  >
    <div
      v-for="(code, i) in colors"
      :key="i"
      class="color-btn"
      :style="{ 'background-color': code }"
      @click="selectColor(i)"
    ></div>
  </div>
</template>
<script>
import { HexGrid, Layout, Hexagon, GridGenerator, HexUtils } from "vue-hexgrid";

String.prototype.hashCode = function () {
  var hash = 0,
    i,
    chr;
  if (this.length === 0) return hash;
  for (i = 0; i < this.length; i++) {
    chr = this.charCodeAt(i);
    hash = (hash << 5) - hash + chr;
    hash |= 0; // Convert to 32bit integer
  }
  return hash;
};

var seed = 1337 ^ 0xdeadbeef; // 32-bit seed with optional XOR value
// Pad seed with Phi, Pi and E.
// https://en.wikipedia.org/wiki/Nothing-up-my-sleeve_number
var rand = sfc32(0x9e3779b9, 0x243f6a88, 0xb7e15162, seed);
function sfc32(a, b, c, d) {
  return function () {
    a >>>= 0;
    b >>>= 0;
    c >>>= 0;
    d >>>= 0;
    var t = (a + b) | 0;
    a = b ^ (b >>> 9);
    b = (c + (c << 3)) | 0;
    c = (c << 21) | (c >>> 11);
    d = (d + 1) | 0;
    t = (t + d) | 0;
    c = (c + t) | 0;
    return (t >>> 0) / 4294967296;
  };
}

function zeroPrefix(num, digit) {
  var zero = "";
  for (var i = 0; i < digit; i++) {
    zero += "0";
  }
  return (zero + num).slice(-digit);
}

export default {
  name: "App",
  components: {
    HexGrid,
    Layout,
    Hexagon,
  },
  data() {
    return {
      hexagons: [],
      selected: -1,
      index: new Map(),
      floatTop: 1,
      floatLeft: 1,
      size: 20,
      sizeInput: 20,
      seedText: "",
      groups: [],
      finish: false,
      timer: null,
      time: "00:00:00.000",
      timeBegan: Date.now(),
      colors: ["#fabed4", "#ffd8b1", "#fffac8", "#aaffc3"],
    };
  },

  computed: {
    styles() {
      return {
        left: this.floatLeft + "px",
        top: this.floatTop + "px",
      };
    },
  },

  created() {
    this.reset();
  },

  methods: {
    reColor() {
      this.finish = true;
      this.hexagons = this.hexagons.map((e) => {
        if (e.group != undefined) {
          e.color = this.colors[this.groups[e.group].color];
          e.strokeColor = "black";
          let neighbours = HexUtils.neighbours(e);
          for (let n of neighbours) {
            let index = this.index[HexUtils.getID(n)];
            if (index != undefined && this.hexagons[index].group != e.group) {
              e.stroke = true;
              if (
                this.groups[this.hexagons[index].group].color ===
                this.groups[e.group].color
              ) {
                e.strokeColor = "red";
                this.finish = false;
              }
              break;
            }
          }
        }
        return e;
      });
      if (this.finish) {
        clearInterval(this.timer);
        this.timer = null;
        window.alert("finished! " + this.time);
      }
    },

    selectColor(colorIndex) {
      this.groups[this.hexagons[this.selected].group].color = colorIndex;
      this.reColor();
    },

    reset() {
      this.size = this.sizeInput;
      seed = this.seedText.hashCode();
      rand = sfc32(0x9e3779b9, 0x243f6a88, 0xb7e15162, seed);
      this.groups = [];
      this.hexagons = GridGenerator.rectangle(this.size, this.size);
      this.index = new Map();
      for (let i = 0; i < this.hexagons.length; i++) {
        this.hexagons[i].color = "gray";
        this.index[HexUtils.getID(this.hexagons[i])] = i;
      }

      this.init(this.size);
      this.reColor();
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      this.timeBegan = new Date();
      this.timer = setInterval(this.clockRunning, 10);
    },

    clockRunning() {
      var currentTime = new Date(),
        timeElapsed = new Date(currentTime - this.timeBegan),
        hour = timeElapsed.getUTCHours(),
        min = timeElapsed.getUTCMinutes(),
        sec = timeElapsed.getUTCSeconds(),
        ms = timeElapsed.getUTCMilliseconds();

      this.time =
        zeroPrefix(hour, 2) +
        ":" +
        zeroPrefix(min, 2) +
        ":" +
        zeroPrefix(sec, 2) +
        "." +
        zeroPrefix(ms, 3);
    },

    init(n) {
      for (let i = 0; i < n; i++) {
        let r = rand();
        console.log(r);
        let index = Math.floor(this.hexagons.length * r);
        let dup = false;
        for (let j = 0; j < this.groups.length; j++) {
          if (this.groups[j].member[0] === index) {
            dup = true;
            break;
          }
        }
        if (!dup) {
          this.groups.push({
            color: Math.floor(65536 * r) % 4,
            border: [index],
            member: [index],
          });
        }
      }
      for (let i = 0; i < this.groups.length; i++) {
        this.hexagons[this.groups[i].member[0]].group = i;
      }

      let full = false;

      while (!full) {
        for (let i = 0; i < this.groups.length; i++) {
          let newborder = [];
          for (let j = 0; j < this.groups[i].border.length; j++) {
            let neighbours = HexUtils.neighbours(
              this.hexagons[this.groups[i].border[j]]
            );
            for (n of neighbours) {
              let index = this.index[HexUtils.getID(n)];
              if (
                index != undefined &&
                this.hexagons[index].group === undefined
              ) {
                this.hexagons[index].group = i;
                newborder.push(index);
                this.groups[i].member.push(index);
              }
            }
          }
          this.groups[i].border = newborder;
        }
        //console.log(this.groups)

        full = true;
        for (let i = 0; i < this.hexagons.length; i++) {
          if (this.hexagons[i].group === undefined) {
            full = false;
            break;
          }
        }
      }
    },

    onClick(event, source) {
      if (source) {
        if (this.selected === this.index[HexUtils.getID(source.hex)]) {
          this.selected = -1;
        } else {
          this.floatTop = event.pageY;
          this.floatLeft = event.pageX;
          this.selected = this.index[HexUtils.getID(source.hex)];
        }
      }
    },
  },
};
</script>

<style>
body {
  background: #e4e7ec;
  color: #263959;
}

svg g :hover {
  cursor: pointer;
}

.color-btn {
  border: 2px solid burlywood;
  width: 40px;
  height: 40px;
}
</style>
